  background-image: linear-gradient(to top, rgb(0 0 0 / 0%), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.6));
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2vmin 0 1vmin 0;
}

.content-toolbar {
  width: 100vw;
  /* max-width: 500px; */
  height: 7vmin;
  padding-right: 3vmin;
  background-image: url(/app/static/media/Logo_wide.35138d32.png);
  background-repeat: no-repeat;
